import { Fragment } from 'react';
import { Helmet } from 'react-helmet';
import Image from 'components/Image';
import Footer from 'components/Footer';
import {
    ProjectContainer,
    ProjectBackground,
    ProjectHeader,
    ProjectSection,
    ProjectSectionContent,
    ProjectImage,
    ProjectSectionHeading,
    ProjectSectionColumns,
    ProjectTextRow,
    ProjectSectionText,
} from 'components/ProjectLayout';
import { useScrollRestore } from 'hooks';
import { media } from 'utils/style';
import prerender from 'utils/prerender';
import covid19 from 'assets/covid19.jpg';
import covid19fg from 'assets/covid19.png';
import vaccine from 'assets/vaccine.png';
import vaccinePage from 'assets/vaccinePage.png';

const title = 'Covid19 Leads';
const description =
    'Design and development of a Covid19 Leads tracker which pulled all relevant tweets from twitter with additional functionality of vaccine notifier.';
const roles = [
    'Front End Development',
    'Minimal Backend Development',
    'Developed using ReactJs and Nodejs',
    'Minimal Design'
];

const ProjectDM = () => {
    useScrollRestore();

    return (
        <Fragment>
            <Helmet>
                <title>Projects | {title}</title>
                <meta name="description" content={description} />
            </Helmet>
            <ProjectContainer>
                <ProjectBackground
                    srcSet={`${covid19} 1080w, ${covid19} 2160w`}
                    placeholder={covid19}
                    entered={!prerender}
                />
                <ProjectHeader
                    title={title}
                    description={description}
                    url="https://covid19leads.herokuapp.com/"
                    roles={roles}
                />
                <ProjectSection first>
                    <ProjectSectionContent>
                        <ProjectImage
                            raised
                            srcSet={`${covid19fg} 1280w, ${covid19fg} 2560w`}
                            placeholder={covid19fg}
                            sizes={`(max-width: ${media.mobile}px) 100vw, (max-width: ${media.tablet}px) 800px, 1000px`}
                            alt="Device Models plugin interface."
                        />
                    </ProjectSectionContent>
                </ProjectSection>
                <ProjectSection>
                    <ProjectTextRow>
                        <ProjectSectionHeading>The Problem</ProjectSectionHeading>
                        <ProjectSectionText>
                            A second wave beginning in March 2021 was much larger than the first
                            , with shortages of vaccines, hospital beds, oxygen cylinders and
                            other medicines in parts of the country.[15] By late April, India led
                            the world in new and active cases. On 30 April 2021, it became the first
                            country to report over 400,000 new cases in a 24-hour period
                        </ProjectSectionText>
                        <ProjectSectionText>
                            The second wave placed a major strain on the healthcare system,
                            including a shortage of liquid medical oxygen due to ignored warnings
                            which began in the first wave itself, logistic issues, and
                            a lack of cryogenic tankers
                        </ProjectSectionText>
                        <ProjectSectionText>
                            It all came down in hands of the people to take care of the situation.
                            Tweets were pouring in for the verified leads on different requirements,
                            which this app is supposed to ease.
                        </ProjectSectionText>
                    </ProjectTextRow>
                </ProjectSection>
                <ProjectSection light>
                    <ProjectSectionColumns>
                        <ProjectTextRow>
                            <ProjectSectionHeading>Vaccination Problem</ProjectSectionHeading>
                            <ProjectSectionText>
                                Due to high demand, the vaccination programme began to be hit with
                                supply issues; exports of the Oxfordâ€“AstraZeneca vaccine were suspended
                                to meet domestic demand, there have been shortages of the raw materials
                                required to manufacture vaccines domestically, while hesitancy and a lack
                                of knowledge among poorer, rural communities has also impacted the programme
                            </ProjectSectionText>
                            <ProjectSectionText>
                                Giving a simple email notification when a vaccine slot was available in the
                                specified area was a much needed functionality.
                            </ProjectSectionText>
                        </ProjectTextRow>
                        <Image
                            srcSet={`${vaccine} 400w, ${vaccine} 898w`}
                            placeholder={vaccine}
                            sizes={`(max-width: ${media.mobile}px) 100vw, (max-width: ${media.tablet}px) 100vw, 50vw`}
                            alt="The Device Models color palette and logo, featuring a low poly monogram to convey its 3D allure."
                        />
                    </ProjectSectionColumns>
                </ProjectSection>
                <ProjectSection>
                    <ProjectSectionContent>
                        <Image
                            srcSet={`${vaccinePage} 400w, ${vaccinePage} 898w`}
                            placeholder={vaccinePage}
                            sizes={`(max-width: ${media.mobile}px) 100vw, (max-width: ${media.tablet}px) 100vw, 50vw`}
                            alt="The Device Models color palette and logo, featuring a low poly monogram to convey its 3D allure."
                        />
                    </ProjectSectionContent>
                </ProjectSection>
            </ProjectContainer>
            <Footer />
        </Fragment>
    );
};

export default ProjectDM;
